import React, { useRef } from 'react';
import './App.css';

import { ThemeProvider, useTheme, useMediaQuery, makeStyles, createMuiTheme } from "@material-ui/core";
import { LanguageProvider } from './providers/LanguageContext';
import { ModalProvider } from './providers/ModalContext';

import NavBar from './components/Navbar';
import Introduction from './components/Introduction';
import About from './components/About';
import Experience from './components/Experience';
import Projects from './components/Projects';
import Contact from './components/Contact';
import Blog from './components/Blog';
import { purple, teal } from '@material-ui/core/colors';

const useStyles = makeStyles({
  title: {
    fontWeight: 'bold',
    fontSize: 52
  },
  subTitle: {
    fontSize: 32
  },
  container: {
    marginTop: 250,
    marginBottom: 250
  },
  textField: {
    background: 'linear-gradient(90deg, #cccccc,#FFFFFF, #cccccc)',
    border: 0,
    borderRadius: 3,
  },
  buttonWrapper: {
    marginTop: 10
  },
  modal: {
    textAlign: 'center',
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    outline: "none"
  },
  paper: {
    padding: '6px 16px',
    color: "white",
    width: "100%"
  },
  navbar: {
    backgroundColor: "#121212",
    color: "white"
  },
  brand: {
    flexGrow: 1
  },
  brandText: {
    cursor: 'pointer'
  }
}, { index: 1 });

const theme = createMuiTheme({
  palette: {
    primary: {
      main: purple[900]
    },
    secondary: {
      main: teal[500]
    },
    background: {
      default: "#121212",
      paper: purple[900]
    },
    text: {
      primary: "#000"
    }
  }
})

function App() {
  const classes = useStyles()

  const isMobile = useMediaQuery(useTheme().breakpoints.down("sm"))

  const aboutRef = useRef(null)
  const experienceRef = useRef(null)
  const projectsRef = useRef(null)
  const contactRef = useRef(null)
  const blogRef = useRef(null)

  const sections = {
    "About": {
      ref: aboutRef
    },
    "Experience": {
      ref: experienceRef
    },
    "Projects": {
      ref: projectsRef
    },
    "Blog": {
      ref: blogRef
    },
    "Contact": {
      ref: contactRef
    }
  }

  return (
    <LanguageProvider >
      <ThemeProvider theme={theme}>
        <ModalProvider classes={classes}>
          <NavBar sections={sections} classes={classes} isMobile={isMobile} />
          <Introduction classes={classes} />
          <About refProp={aboutRef} contactRef={contactRef} classes={classes} />
          <Experience refProp={experienceRef} classes={classes} />
          <Projects refProp={projectsRef} classes={classes} isMobile={isMobile} />
          <Blog refProp={blogRef} classes={classes} />
          <Contact refProp={contactRef} classes={classes} />
        </ModalProvider>
      </ThemeProvider>
    </LanguageProvider>
  )
}

export default App;
